import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store'
// import Login from '../views/Login.vue'
// import Student from '../views/Student.vue'
// import Teacher from '../views/Teacher.vue'
// import InternshipApplication from '../views/InternshipApplication.vue'
// import CompanyApplication from '../views/CompanyApplication.vue'
// import PrivacyPolicy from '../views/PrivacyPolicy.vue'
// import NotFound from '../views/404.vue'
// import PDF from '../views/Pdf.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'login',
    // component: Login,
    component: () => import(/* webpackChunkName: "Login" */ '../views/Login.vue'),
    beforeEnter(to, from, next) {
      if (store.getters.isAuthenticated) {
        if (store.getters.role === 'student') {
          next({name: 'opilane'})
        } else {
          next({name: 'koordinaator'})
        }
      } else {
        next();
      }
    }
  },
  {
    path: '/opilane',
    name: 'opilane',
    // component: Student,
    component: () => import(/* webpackChunkName: "student" */ '../views/Student.vue'),
    beforeEnter(to, from, next) {
      if (store.getters.isAuthenticated) {
        if (store.getters.role === 'student') {
          next()
        } else {
          next(from)
        }
      } else {
        next({name: 'login'})
      }
    }
  },
  {
    path: '/koordinaator',
    name: 'koordinaator',
    // component: Teacher,
    component: () => import(/* webpackChunkName: "teacher" */ '../views/Teacher.vue'),
    beforeEnter(to, from, next) {
        if (store.getters.isAuthenticated) {
          if (store.getters.role === 'admin') {
            next()
          } else {
            next(from)
          }
        } else {
          next({name: 'login'})
        }
    }
  },
  {
    path: '/praktikataotlus',
    name: 'praktikataotlus',
    // component: InternshipApplication,
    component: () => import(/* webpackChunkName: "praktikataotlus" */ '../views/InternshipApplication.vue'),
    beforeEnter(to, from, next) {
      if (store.getters.isAuthenticated) {
        if (store.getters.role === 'student') {
          next()
        } else {
          next(from)
        }
      } else {
        next({name: 'login'})
      }
    }
  },
  {
    path: '/taotlus/:id',
    name: 'taotlus',
    // component: CompanyApplication,
    component: () => import(/* webpackChunkName: "ettevõttetaotlus" */ '../views/CompanyApplication.vue')
  },
  {
    path: '/pdf/:id',
    name: 'pdf',
    // component: PDF,
    component: () => import(/* webpackChunkName: "pdf" */ '../views/Pdf.vue'),
    meta: { hideHeader: true, hideFooter: true}
  },
  {
    path: '/privaatsustingimused',
    name: 'privaatsustingimused',
    // component: PrivacyPolicy,
    component: () => import(/* webpackChunkName: "PrivacyPolicy" */ '../views/PrivacyPolicy.vue')

  },
  {
    path: '/*',
    name: '404',
    // component: NotFound,
      component: () => import(/* webpackChunkName: "notFound" */ '../views/404.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
